import React, { Component } from 'react';
import styled from 'styled-components';
import { extraIcons } from './media';
import { epochTimeToLocalTime } from './getDate';

const Wrapper = styled.div`
  grid-area: extra;
  width: 100%;
  padding: 30px 30px 40px 30px;
`;

const Grid = styled.div`
  width: 100%;
  display: grid;
  grid-gap: 8px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  @media screen and (min-width: 768px) {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  }

  @media screen and (min-width: 1024px) {
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 32px;
  }
`;

const Element = styled.div`
  img {
    width: 100%;
    max-width: 100px;
  };
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
`;

class CurrentWeatherExtra extends Component {
  render() {
    const { humidity, pressure, visibility, windSpeed, 
      sunrise, sunset, imperial } = this.props;
    
    const sunriseLocal = epochTimeToLocalTime(sunrise);
    const sunsetLocal = epochTimeToLocalTime(sunset);

    return (
      <Wrapper>
        <Grid>
          <Element>
            <img src={extraIcons[`humidity`]} alt='humidity'/>
            <h4>humidity</h4>
            <p>{humidity} %</p>
          </Element>
          <Element>
            <img src={extraIcons[`wind`]} alt='wind'/>
            <h4>wind</h4>
            <p>{windSpeed}m/s</p>
          </Element>
          <Element>
            <img src={extraIcons[`pressure`]} alt='pressure'/>
            <h4>pressure</h4>
            <p>{pressure}hPa</p>
          </Element>
          <Element>
            <img src={extraIcons[`visibility`]} alt='eye'/>
            <h4>visibility</h4>
            <p>{visibility}m</p>
          </Element>
          <Element>
            <img src={extraIcons[`sunrise`]} alt='sunrise'/>
            <h4>sunrise</h4>
            <p>{sunriseLocal}</p>
          </Element>
          <Element>
            <img src={extraIcons[`sunset`]} alt='sunset'/>
            <h4>sunset</h4>
            <p>{sunsetLocal}</p>
          </Element>
        </Grid>
      </Wrapper>
    );
  }
}

export default CurrentWeatherExtra;
