import React, { Component } from 'react';
import styled from 'styled-components';
import { backgrounds } from './media';
import alphaColorBg from '../assets/30_percent_opacity_black.png';

const BackgroundStyled = styled.div`
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  background-image: url(${alphaColorBg});
  background-size: cover;
  background-attachment: fixed;

  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
  > img:first-child {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  > img:last-child {
    position: absolute;
    // top: 20vh;
    left: 0;
    width: 100%;
    object-fit: contain;
  }
}
`;

class Background extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
      animate: false,
    }
  }
  
  componentDidMount() {
    this.setState({
      animate: true,
    })
  }

  render() {
    const { icon } = this.props;
    const { animate } = this.state; 
    return (
      <BackgroundStyled>
        <img src={backgrounds[`bg${icon}1`]} alt=""/>
        <img class={`${animate? 'animate__animated animate__fadeInDown' : null}`} src={backgrounds[`bg${icon}2`]} alt=""/>
      </BackgroundStyled>
    )
  }
}

export default Background
